import React, { useState, useContext, useEffect } from 'react'
import { Link, useLocation, matchPath } from 'react-router-dom'
import { resources } from './resources.js'
import { allFilters } from './allFilters.js'
import InputFilter from './InputFilter.js'
import EnumFilter from './EnumFilter.js'
import DateFilter from './DateFilter.js'
import AppliedFilter from './AppliedFilter.js'
import { Context } from '../../context.js'
import './filters.css'

export default function Filters() {
  const [filters, setFilters] = useState([])
  const { setParams, params } = useContext(Context)
  const location = useLocation()

  // –ê–∫—Ç–∏–≤–Ω–æ—Å—Ç—å –∫–Ω–æ–ø–∫–∏ Apply
  let classApplyBtn = 'filters__button-section__button'
  if (!filters.length)
    classApplyBtn =
      'filters__button-section__button filters__button-section__button--disabled'

  // –ê–∫—Ç–∏–≤–Ω–æ—Å—Ç—å –∫–Ω–æ–ø–∫–∏ Clear
  let classClearBtn = 'filters__button-section__button'
  if (!params.length)
    classClearBtn =
      'filters__button-section__button filters__button-section__button--disabled'

  // –ó–∞–≥—Ä—É–∑–∫–∞ —Ñ–∏–ª—å—Ç—Ä–æ–≤ –∏–∑ url –ø–æ—Å–ª–µ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è —Å—Ç—Ä–∞–Ω–∏—Ü—ã –∏–ª–∏ —Å–º–µ–Ω—ã –∫–æ–Ω—Ç–µ–Ω—Ç–∞
  useEffect(() => {
    const curentURL = new URL(window.location)
    let curentParams = []
    for (let [name, val] of curentURL.searchParams) {
      curentParams = [...curentParams, { type: name, value: val }]
    }
    setParams(curentParams)
  }, [location.pathname])

  // –ü—Ä–∏–º–µ–Ω–µ–Ω–∏–µ —Ñ–∏–ª—å—Ç—Ä–æ–≤
  const filtersSubmit = () => {
    if (filters.length) {
      setParams((curentParams) =>
        curentParams
          // —É–¥–∞–ª—è–µ–º –ø–∞—Ä–∞–º–µ—Ç—Ä—ã, –∫–æ—Ç–æ—Ä—ã–µ –±—É–¥–µ–º —Å–∞–±–º–∏—Ç–∏—Ç—å
          .filter((p) => {
            if (filters.findIndex((f) => f.type === p.type) === -1) return true
            else return false
          })
          // –¥–æ–±–∞–≤–ª—è–µ–º —Ç–µ–∫—É—â–∏–µ –≤—ã–±—Ä–∞–Ω–Ω—ã–µ
          .concat(filters)
      )
      setFilters([])
    }
  }

  // —É—á—Ç–∞–Ω–æ–≤–∫–∞ –ø–∞—Ä–∞–º–µ—Ç—Ä–æ–≤ –≤ url
  const setFilterParams = () => {
    const newUrl = new URL(window.location)
    filters.forEach((p) => newUrl.searchParams.set(p.type, p.value))
    return newUrl.search
  }

  // –ù–∞–ª–∏—á–∏–µ –¥–æ—Å—Ç—É–ø–Ω—ã—Ö —Ñ–∏–ª—å—Ç—Ä–æ–≤ –¥–ª—è –∫–æ–Ω—Ç–µ–Ω—Ç–∞
  const hasFilters = () => {
    let hasFiltersFlag = false
    resources.forEach((res) => {
      if (
        matchPath(location.pathname, {
          path: res.pathname,
          exact: true,
          strict: false,
        })
      )
        if (res.filters.length) hasFiltersFlag = true
    })
    return hasFiltersFlag
  }

  return (
    <div className="filter-search-block">
      <form className="filters">
        <h2 className="filters__header">Filters</h2>

        <div className="params-list">
          {params.map((param) => (
            <AppliedFilter key={param.type} param={param} />
          ))}
        </div>

        <hr />

        {!hasFilters() ? (
          <div className="filters__item filters__no-resources">
            Filters not found for curent resource
          </div>
        ) : null}
        {/* —Ü–∏–∫–ª –ø–æ –º–∞—Å—Å–∏–≤—É —Ä–µ—Å—É—Ä—Å–æ–≤ –¥–ª—è –ø–æ–∏—Å–∫–∞ –¥–æ—Å—Ç—É–ø–Ω—ã—Ö —Ñ–∏–ª—å—Ç—Ä–æ–≤ */}
        {resources.map((res) => {
          if (
            matchPath(location.pathname, {
              path: res.pathname,
              exact: true,
              strict: false,
            }) // –µ—Å–ª–∏ —Å–æ–≤–ø–∞–¥–∞–µ—Ç –ø—É—Ç—å
          )
            // –ø—Ä–æ—Ö–æ–¥ –ø–æ –Ω–∞–π–¥–µ–Ω–Ω—ã–º –¥–ª—è —Ä–µ—Å—É—Ä—Å–∞ —Ñ–∏–ª—å—Ç—Ä–∞–º
            return res.filters.map((fItem) =>
              // —Ü–∏–∫–ª –ø–æ –º–∞—Å—Å–∏–≤—É –≤—Å–µ—Ö –≤–æ–∑–º–æ–∂–Ω—ã—Ö —Ñ–∏–ª—å—Ç—Ä–æ–≤
              allFilters.map((filter) => {
                if (filter.name === fItem)
                  // –æ—Ç–æ–±—Ä–∞–∂–µ–Ω–µ —Ñ–∏–ª—å—Ç—Ä–∞ –≤ –∑–∞–≤–∏—Å–∏–º–æ—Å—Ç–∏ –æ—Ç filter.type
                  switch (filter.type) {
                    default:
                      return null
                    case 'Input':
                      return (
                        <InputFilter
                          filter={filter}
                          key={filter.name}
                          setFilters={setFilters}
                        />
                      )
                    case 'Enum':
                      return (
                        <EnumFilter
                          filter={filter}
                          key={filter.name}
                          setFilters={setFilters}
                        />
                      )
                    case 'Date':
                      return (
                        <DateFilter
                          filter={filter}
                          key={filter.name}
                          setFilters={setFilters}
                        />
                      )
                  }
                else return null
              })
            )
          else return null
        })}

        <hr />

        <div className="filters__button-section ">
          <Link
            to={{
              pathname: location.pathname,
              search: filters.length
                ? setFilterParams()
                : window.location.search,
            }}
            className={classApplyBtn}
            onClick={filtersSubmit}
          >
            Apply
          </Link>

          <Link
            to={{
              pathname: location.pathname,
            }}
            className={classClearBtn}
            onClick={() => {
              if (params.length) setParams([])
            }}
          >
            Clear all
          </Link>
        </div>
      </form>
    </div>
  )
}
